.category {
  &__photos {
    @include page-grid;

    margin-bottom: 16px;
  }

  &__link {
    position: relative;
    &--wide {
      @include on-tablet {
        grid-column: span 4;
      }

      @include on-desktop {
        grid-column: span 8;
      }
    }
    &--square {
      height: 0;
      padding-bottom: 100%;

      @include on-tablet {
        grid-column: span 2;
      }

      @include on-desktop {
        grid-column: span 4;
      }
    }
  }

  &__photo {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;

    @include hover(transform, scale(1.05))
  }

  &__title {
    color: $main-text-color;
    font-size: 24px;
    font-weight: 700;
    line-height: 140%;
    margin-block: 16px 0;
  }

  &__descr {
    margin-block: 8px 0;
    font-weight: 400;
  }
}
